//import { getLiquidityInfo2 } from '../v1/api/rest_api';
import {
  DeriEnv
} from '../shared/config';
import {
  LITE_VERSIONS,
  checkChainId,
  checkAddress,
  getLpConfigList,
} from '../shared/config';
import {
  catchApiError,
  deriToNatural,
  getNetworkName,
  max,
  bg,
} from '../shared/utils';
import {
  databaseFactory,
} from '../shared/factory';
import { getLiquidityInfo2 } from '../v1/api';
import {
  getLiquidityInfoV2l,
} from '../v2_lite/api';
import { AppError } from '../shared/error/error';
import {
  getPoolVersionId,
} from '../shared/config/pool';

import { api as apiV2lDpmm } from '../v2_lite_dpmm/api'
import { api as apiV2Dpmm } from '../v2_dpmm/api'
import { api as apiOption } from '../option/api'
import { getPoolConfigList } from './config_api';

export const getLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress,
  bTokenId,
) => {
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    return { success: false, response: versionId.response }
  }
  if (LITE_VERSIONS.includes(versionId)) {
    return getLiquidityInfoV2l(chainId, poolAddress, accountAddress)
  } else if (versionId === 'option') {
    return apiOption.getLiquidityInfo(chainId, poolAddress, accountAddress)
  } else if (versionId === 'v2_lite_dpmm') {
    return apiV2lDpmm.getLiquidityInfo(chainId, poolAddress, accountAddress)
  } else if (versionId === 'v2_dpmm') {
    return apiV2Dpmm.getLiquidityInfo(chainId, poolAddress, accountAddress, bTokenId)
  }
  //if (bTokenId === undefined) {
    return getLiquidityInfo2(chainId, poolAddress, accountAddress)
  // } else {
  //   return getLiquidityInfoV2(chainId, poolAddress, accountAddress, bTokenId)
  // }
};

export const getPoolLiquidity = async (
  chainId,
  poolAddress,
) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId);
    poolAddress = checkAddress(poolAddress);
    let result = await getPoolConfigList(DeriEnv.get())
    let configs = []
    if (result.success) {
      configs = result.response.data
    }
    // add lp config
    configs = configs.concat(await getLpConfigList(DeriEnv.get()));
    const config = configs.find((v) => v.pool === poolAddress);
    if (!config) {
      throw new AppError('CONFIG_NOT_FOUND', {
        name: 'getPoolConfig',
        args: [DeriEnv.get(), poolAddress],
      });
    }
    const db = databaseFactory();
    let res;
    if (config.bTokens.length > 1 && config.type !== 'lp') {
      const bTokenIds = config.bTokens.map((b) => b.bTokenId);
      const key = bTokenIds.reduce(
        (acc, bTokenId) => [
          ...acc,
          `${chainId}.${poolAddress}.liquidity${bTokenId}`,
        ],
        [`${chainId}.${poolAddress}.liquidity`]
      );
      const result = await db.getValues(key);
      const totalValue = deriToNatural(result[0]).toString();
      const bTokens = bTokenIds.reduce((acc, bTokenId, index) => {
        return [
          ...acc,
          {
            bTokenId,
            liquidity: deriToNatural(result[1 + index]).toString(),
          },
        ];
      }, []);
      res = {
        totalValue,
        bTokens,
      };
    } else {
      const result = await db.getValues([
        `${chainId}.${poolAddress}.liquidity`,
      ]);
      const liquidity = deriToNatural(result[0]).toString();
      res = {
        totalValue: liquidity,
        bTokens: [{ liquidity }],
      };
    }
    return res;
  });
};

export const getPoolInfoApy = async (chainId, poolAddress) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId);
    poolAddress = checkAddress(poolAddress);
    let result = await getPoolConfigList(DeriEnv.get());
    let configs = [];
    if (result.success) {
      configs = result.response.data;
    }
    // add lp config
    configs = configs.concat(await getLpConfigList(DeriEnv.get()));
    const config = configs.find((v) => v.pool === poolAddress);
    if (!config) {
      throw new AppError('CONFIG_NOT_FOUND', {
        name: 'getPoolConfig',
        args: [DeriEnv.get(), poolAddress],
      });
    }
    const db = databaseFactory(true);
    let res;
    if (config.bTokens.length > 1 && config.type !== 'lp') {
      const bTokenIds = config.bTokens.map((b) => b.bTokenId);
      const key = bTokenIds.reduce(
        (acc, bTokenId) => [
          ...acc,
          ...[
            `${getNetworkName(chainId)}.${poolAddress}.apy${bTokenId}`,
            `${getNetworkName(chainId)}.${poolAddress}.V2.multiplier${bTokenId}`,
          ],
        ],
        []
      );
      const result = await db.getValues(key);
      const apys = result.reduce(
        (acc, a, index) => [
          ...acc,
          ...(index % 2 === 0 ? [deriToNatural(a)] : []),
        ],
        []
      );
      const maxApy = max(...apys).toString();
      const bTokens = bTokenIds.reduce((acc, bTokenId, index) => {
        return [
          ...acc,
          {
            bTokenId,
            apy: deriToNatural(result[index * 2]).toString(),
            multiplier: deriToNatural(result[index * 2 + 1]).toString(),
          },
        ];
      }, []);
      res = {
        maxApy,
        bTokens,
      };
    } else if (config.type === 'lp') {
      const result = await db.getValues([
        `${getNetworkName(chainId)}.${poolAddress}.apy`,
        `${getNetworkName(chainId)}.${poolAddress}.apy2`,
      ]);
      const [apy1, apy2] = [
        deriToNatural(result[0]).toString(),
        deriToNatural(result[1]).toString()
      ]
      res = {
        maxApy: max(bg(apy1), bg(apy2)).toString(),
        bTokens: [{ apy: apy1 }, { apy: apy2 }],
      };
    } else {
      const result = await db.getValues([`${getNetworkName(chainId)}.${poolAddress}.apy`]);
      const apy = deriToNatural(result[0]).toString();
      res = {
        maxApy: apy,
        bTokens: [{ apy, multiplier: '1' }],
      };
    }
    return res;
  });
};
