import { getTradeHistory } from "../lib/web3js/index";
import ApiProxy from "./ApiProxy";


export default class History {

   async load(wallet,pool,isOptions){
      if(wallet && wallet.isSupportChain(isOptions) && wallet.detail.chainId && pool && pool.address){
         const all = await ApiProxy.request('getTradeHistory',[wallet.detail.chainId,pool.pool,wallet.detail.account,pool.symbolId]); 
         return all;
      } else {
         return []
      }
   }
}