import styled from 'styled-components'
import langIcon from '../../assets/img/lang-icon.svg'

const Wrapper = styled.div`
  display : flex;
  align-items: center;
`

const Panel = styled.div`
  display : none;
`

export default function LangSelector(){
  return (
    <Wrapper>
      <img src={langIcon} alt='lang Selector'/>
      <Panel>
        <div>En</div>
      </Panel>
    </Wrapper>
  )
}